<template>
  <div class="home">
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span>Hex Convert</span>
      </div>
      <div class="text">
        <div class="item">
          <el-input style="width: 280px;" v-model="hexToStrText" placeholder="hex to string" @change="hexToStr(hexToStrText)" clearable></el-input>
          <el-input style="width: 280px;" v-model="hexToStrResult" :readonly="true"></el-input>
        </div>
        <div class="item">
          <el-input style="width: 280px;" v-model="hexToNumText" placeholder="hex to number" @change="hexToNum(hexToNumText)" clearable></el-input>
          <el-input style="width: 280px;" v-model="hexToNumResult" :readonly="true"></el-input>
        </div>

        <div class="item">
          <el-input style="width: 280px;" v-model="numToHexText" placeholder="number to hex" @change="numToHex(numToHexText)" clearable></el-input>
          <el-input style="width: 280px;" v-model="numToHexResult" :readonly="true"></el-input>
        </div>

        <div class="item">
          <el-input style="width: 280px;" v-model="strToHexText" placeholder="string to hex" @change="strToHex(strToHexText)" clearable></el-input>
          <el-input style="width: 280px;" v-model="strToHexResult" :readonly="true"></el-input>
        </div>
        
      </div>
    </el-card>
  </div>
</template>

<script>

const ethers = require('ethers');

export default {
  name: 'Home',
  data() {
    return {
      hexToStrText: '',
      hexToStrResult: '',
      hexToNumText: '',
      hexToNumResult: '',
      numToHexText: '',
      numToHexResult: '',
      strToHexText: '',
      strToHexResult: '',
    }
  },
  methods: {
      hexToStr(value) {
        let str = ethers.utils.parseBytes32String(value);
        this.hexToStrResult = str;
      },
      hexToNum(value) {
        let number = ethers.utils.formatUnits(value, 0);
        console.log(number);
        this.hexToNumResult = number;
      },
      numToHex(value) {
        let hex = ethers.utils.parseUnits(value, 0);
        this.numToHexResult = hex.toHexString();
      },
      strToHex(value) {
        let hex = ethers.utils.formatBytes32String(String(value));
        this.strToHexResult = hex;
      },
    }
 
}
</script>

