  .text {
    font-size: 14px;
    text-align: center;
  }

  .clearfix:before,
  .clearfix:after {
    display: table;
    content: "";
  }
  .clearfix:after {
    clear: both
  }

  .item {
    margin-bottom: 30px;
  }

  .box-card {
    width: 480px;
  }
</style>
